/* youtuber等
   Add this css for responsive embedded videos in your .css file. */

   .embed-container {
    position: relative;
    padding-bottom: 56.25%;
    height: 0;
    overflow: hidden;
    max-width: 100%;
  }
  
  .embed-container iframe,
  .embed-container object,
  .embed-container embed {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  


/* 購物車振動
--------------------------------------------------------------*/

.shake {
    /* Start the shake animation and make the animation last for 0.5 seconds */
    animation: shake 2s;
  
  }
  
  @keyframes shake {
    0% {
      transform: translate(20px, 0px) rotate(0deg);
    }
  
    10% {
      transform: translate(-20px, 0px) rotate(-1deg);
    }
  
    20% {
      transform: translate(20px, 0px) rotate(1deg);
    }
  
    30% {
      transform: translate(-20px, 0px) rotate(0deg);
    }
  
    40% {
      transform: translate(20px, 0px) rotate(1deg);
    }
  
    50% {
      transform: translate(-20px, 0px) rotate(-1deg);
    }
  
    60% {
      transform: translate(20px, 0px) rotate(0deg);
    }
  
    70% {
      transform: translate(-10px, 0px) rotate(-1deg);
    }
  
    80% {
      transform: translate(10px, 0px) rotate(1deg);
    }
  
    90% {
      transform: translate(-5px, 0px) rotate(0deg);
    }
  
    100% {
      transform: translate(3px, 0px) rotate(-1deg);
    }
  }



  


  

/* Cart的Table
-------------------------------------------------- */


div.simpleCart_items table {
  width: 100%;
  margin-bottom: 20px;
  line-height: 3;
  border-collapse: collapse;
  border-spacing: 0;
}

div.simpleCart_items table {
  tbody {
    border-bottom: 1px solid #ddd;
  }

  th {
    text-align: left;
  }

  td {
    padding: 0px 0;
    border-top: 1px solid #ddd;
  }

  th {
    font-weight: 400;
  }

  .item-quantity {
    text-align: center;
  }

  @media (min-width: 991px) {
  .item-session {
    text-align: center;
  }
 }
  /*
Max width before this PARTICULAR table gets nasty
This query will take effect for any screen smaller than 760px
and also iPads specifically.
*/
  @media (max-width: 990px) {

    // @media only screen and (max-width: 760px), (min-device-width: 768px) and (max-device-width: 1024px) {

    /* Force table to not be like tables */
    table,
    tbody,
    td,
    th,
    thead,
    tr {
      display: block;
      line-height: 1.5;
    }

    /* Hide table headers (but not display: none;, for accessibility) */
    thead tr {
      position: absolute;
      top: -9999px;
      left: -9999px;
    }

    tr {
      margin-bottom: 8px;
      border: 1px solid #ccc;
    }

    tbody {
      border-bottom: 0px;
    }

    td {
      /* Behave  like a "row" */
      position: relative;
      padding-left: 50px;
      border: 0;
      border-bottom: 0px solid #eee;
    }
    
    
    td:before {
      /* Now like a table header */
      position: absolute;
      /* Top/left values mimic padding */
      top: 0px;
      left: 6px;
      width: 45%;
      padding-right: 10px;
      white-space: nowrap;
    }


    .item-quantity {
      text-align: left;
    }

   
    // 放在同一個row
    .item-decrement,
    .item-quantity,
    .item-increment,
    .item-remove {
      display: inline-block;
    }

    //讓左邊從td的50px減少
    .item-quantity,
    .item-increment {
      padding-left: 20px;
    }

    .item-remove {  
      float: right;
      margin-right: 10px;
    }
   
    // 不出現以下
    td.item-total,
    td.item-price {
      display: none;
    }

    /*
  Label the data
  */
    td:nth-of-type(1):before {
      content: '名稱:';
    }

    td:nth-of-type(2):before {
      content: '類型:';
    }

    td:nth-of-type(3):before {
      content: '日期:';
    }

    td:nth-of-type(4):before {
      content: '場次:';
    }

    td:nth-of-type(5):before {
      content: '數量:';
    }

    

  }
}


