/* 定義為1:1的圖片 */
.sc {
  max-width: 100%;
  height: 0;
  padding-bottom: 95%; /* 1:1 寬高比 */
  position: relative;
  overflow: hidden;
}

.s-img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100% !important;
  object-fit: cover; /* 保持圖像比例並填滿容器 */
}





/*圖放大*/
.ps:hover .cs {
  transform: scale(1.2);
  transition: all 0.5s;
}



