/* 限制簡介為一行並隱藏多餘的內容 */
.post-excerpt {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2; /* 限制顯示的行數 */
  -webkit-box-orient: vertical;
}

.post-excerpt1 {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 6; /* 限制顯示的行數 */
  -webkit-box-orient: vertical;
}


